import { createSlice } from '@ttungbmt/redux-toolkit'

export const KEY = 'pcd'

let initialState = {
    odich: {
        radius: 200,
        combineCir: true
    },
}

const reducers = {
    setCombineCir: (state, {payload}) => {
        state.odich.combineCir = payload
    }
}

const odichSlice = createSlice({
    name: KEY,
    initialState,
    reducers,
})


export default odichSlice