import React from 'react'
import MapsContainer from './Container'

import OdichPage from './views/odich/index'
import OdichViewPage from './views/odich/view/index'
import OdichListPage from './views/odich/list/index'
import SxhPage from './views/ds-sxh/index'
import HomePage from './views/home/index'
import ThongkePage from './views/thongke/index'
import DncPage from './views/pt-nguyco/index'

export const MapsConfig = {
    routes: [
        {
            path: '/maps',
            component: MapsContainer,
            routes: [
                {
                    path: '/maps/ds-sxh',
                    component: SxhPage
                },

                {
                    path: '/maps/odich/view/:id',
                    component: OdichViewPage
                },
                {
                    path: '/maps/odich/list',
                    component: OdichListPage
                },
                {
                    path: '/maps/odich',
                    component: OdichPage
                },
                {
                    path: '/maps/pt-nguyco',
                    component: DncPage
                },
                {
                    path: '/maps/thongke',
                    component: ThongkePage
                },
                {
                    path: '/',
                    component: HomePage
                },
            ]
        },

    ]
}