import React, { memo, useContext } from 'react'
import { get } from '@ttungbmt/utils'
import { useDidMount } from '@ttungbmt/react-hooks'

import { FormContext, useForm } from "react-hook-form"
import { setFormValues, submitForm } from '@core/components/Form'
import TableBody from './TableBody'

import { ContainerContext } from "../../../ContainerProvider"
import { FormSearch, FormModal, NavList } from '../../../components'
import { getInfoTotal, Table } from '../../../components/ListTable'

const KEY = 'odich'

function DncPage({SidebarLayout, defaultValues}) {
    const {getForm, actions: acts} = useContext(ContainerContext)
    const form = getForm(KEY)

    const methods = useForm({defaultValues})

    useDidMount(() => {
        if(form.values) setFormValues(form.values, methods)
    })

    const onSubmit = values => acts.setFormValues({key: KEY, values})

    return (
        <FormContext {...methods} >
            <SidebarLayout
                title={` dch ` + getInfoTotal(form)}
                icon={`icon-brain`}
                subTitle={<NavList name={KEY} form={form} methods={methods} pageQl={'/admin/odich-sxh'}/>}
                content={(
                    <>
                        <FormModal name={KEY} form={form} methods={methods} html={get(form, 'filter', '')}/>
                        <FormSearch form={form} {...methods} onSubmit={onSubmit}/>
                        <Table name={KEY} url={'/sxh/maps/ds-odich'} form={form}  style={{minWidth: 1300}}>
                            <TableBody/>
                        </Table>
                    </>
                )}
            >
            </SidebarLayout>
        </FormContext>
    )
}

export default memo(DncPage)

