import React, { memo } from 'react'
import { Row, Col, Form } from 'react-bootstrap'
import { Field, submitForm } from '@core/components/Form'
import { Button, BtnSubmit } from "@core/components"

import { getters as gt } from "app/Store"

function FormSearch({handleSubmit, register, onSubmit, onReset, loading}) {
    let dm_quan = gt.getCat('dm_quan'),
        dm_phuong = gt.getCat('dm_phuong')

    return (
        <Form onSubmit={handleSubmit(onSubmit)}>
            <div style={{padding: '10px 10px 0 10px'}}>
                <Row>
                    <Col md={6}>
                        <Field
                            id="drop-quan"
                            as="select"
                            label="Qun huyn"
                            name="maquan"
                            placeholder="Chn qun huyn..."
                            options={dm_quan}
                        />
                    </Col>
                    <Col md={6}>
                        <Field
                            id="drop-phuong"
                            as="select"
                            label="Phng x"
                            name="maphuong"
                            placeholder="Chn phng x..."
                            options={dm_phuong}
                            depends={['drop-quan']}
                            url={`/api/dm/phuong?role=true`}
                        />
                    </Col>
                </Row>
                <Row>
                    <Col md={6}>
                        <Field as="datepicker" label="Thi gian" name="date_from" placeholder="T ngy"/>
                    </Col>
                    <Col md={6}>
                        <Field as="datepicker" label="&nbsp;" name="date_to" placeholder="n ngy"/>
                    </Col>
                </Row>
                <div className="text-right">
                    <Button variant="contained" className="mr-1" onClick={onReset}>Xa</Button>
                    <BtnSubmit id="btn-submit-form" type="submit" loading={loading}>Tm kim</BtnSubmit>
                </div>
            </div>
            <div className="clearfix"/>
            <hr className="m-2"/>
        </Form>
    )
}

export default memo(FormSearch)