import React, {memo} from 'react'
import { InnerHTML } from '@core/components'
import { useDispatch } from '@ttungbmt/redux-toolkit'
import { get, map, isEmpty, isString } from '@ttungbmt/utils'
import { actions as cabenhActs } from "app/reducers/cabenh"

import Cell from "../../components/Cell"

function TableBody({form}) {
    const dispatch = useDispatch()
    const columns = get(form, 'columns')
    const data = get(form, 'data')
    if (!data && isEmpty(get(data, 'data'))) return <></>


    return (
        <tbody>
        {map(data, (i, k1) => {
            let onRowClick = () => dispatch(cabenhActs.clickDnc(i)),
                cellOptions = {
                    items: [
                        {title: 'nh v', color: 'text-success', icon: 'icon-location4', visible: !!i.geometry, onClick: onRowClick},
                        {
                            title: 'Xem chi tit',
                            color: 'text-primary',
                            icon: 'icon-pencil7',
                            href: ({gid}) => `/pt_nguyco/default/update?id=${gid}`
                        },
                    ]
                }


            return (
                <tr key={k1} onClick={onRowClick}>
                    {columns.map((props, k2) => <Cell key={k2} index={k2} col={props} options={cellOptions} model={i}/>)}
                </tr>
            )
        })}
        </tbody>
    )
}

export default memo(TableBody)