import React, { memo, useContext } from 'react'
import { get } from '@ttungbmt/utils'
import { useDidMount, useQueryString } from '@ttungbmt/react-hooks'
import { useDispatch } from '@ttungbmt/redux-toolkit'

import { FormContext, useForm } from "react-hook-form"
import { FormSearch, FormModal, NavList } from '../../components'

import { setFormValues, submitForm } from '@core/components/Form'
import { ContainerContext } from "../../ContainerProvider";
import { getInfoTotal, Table } from '../../components/ListTable'
import { actions as cabenhActs } from 'app/reducers/cabenh'
import TableBody from "./TableBody"

// import { FormContext } from "@core/components/Form";


const KEY = 'cabenh_sxh'

function SxhPage({SidebarLayout, defaultValues}) {
    const dispatch = useDispatch()
    const query = useQueryString()
    const {getForm, actions: acts} = useContext(ContainerContext)
    const form = getForm(KEY)

    const methods = useForm({defaultValues})

    useDidMount(() => {
        if (form.values) setFormValues(form.values, methods)
        if (query.id) dispatch(cabenhActs.showPopCabenh(query))
    })

    const onSubmit = values => {
        acts.setFormValues({key: KEY, values})
    }

    return (
        <FormContext {...methods} >
            <SidebarLayout
                title={`Ca bnh SXH ` + getInfoTotal(form)}
                icon={`icon-brain`}
                subTitle={<NavList name={KEY} form={form} methods={methods} pageQl={'/admin/cabenh-sxh'}/>}
                content={(
                    <>
                        <FormModal name={KEY} form={form} methods={methods} html={get(form, 'filter', '')}/>
                        <FormSearch form={form} {...methods} onSubmit={onSubmit}/>
                        <Table name={KEY} url={'/sxh/maps/ds-sxh'} form={form} style={{minWidth: 1800}}>
                            <TableBody/>
                        </Table>
                    </>
                )}
            >
            </SidebarLayout>
        </FormContext>
    )
}

export default memo(SxhPage)

