import { createSlice } from '@ttungbmt/redux-toolkit'

export const name = 'layout'

let initialState = {
    fullscreen: false,

    map: {
        fullscreen: false,
    },
    header: {
        size: 55,
        menuItems: [
            {url: '/maps', text: 'Home'},
            {url: '/maps/ds-sxh', text: 'Ca bnh'},
            {url: '/maps/odich', text: ' dch'},
            {url: '/maps/pt-nguyco', text: 'im nguy c'},
            {url: '/maps/thongke', text: 'Thng k',},
        ]
    },
    sidebar: {
        size: 450,
        maximumSize: 600,
        handleSize: 5,
    },
    footer: {
        maximumSize: 350,
        size: 0
    }
}

const toggleDiv = (state, name) => {
    state[name].size = state[name].size === 0 ? initialState[name].size : 0
}

const layoutSlice = createSlice({
    name,
    initialState,
    reducers: {
        toggleSidebar(state) {
            toggleDiv(state, 'sidebar')
        },
        toggleHeader(state) {
            toggleDiv(state, 'header')
        },
        toggleAppFullscreen(state) {
            state.fullscreen = !state.fullscreen
        },
        toggleMapFullscreen(state){
            state.map.fullscreen = !state.map.fullscreen
        }

    },
})

export default layoutSlice