import React, { memo } from 'react'
import { useHistory } from "react-router-dom"
import * as Space from "react-spaces"
import { useSlot } from "@ttungbmt/redux-layout"
import Box from '@material-ui/core/Box'
import { isBoolean } from '@ttungbmt/utils'

function Title({title, icon, back, rightTitle}) {
    let history = useHistory()

    return (
        <>
            {back ? (
                <a className="cursor-pointer" onClick={() => isBoolean(back) ? history.goBack() : history.push(back)}>
                    <i className="icon-arrow-left52 ml-2"/>
                </a>
            ) : (
                <span className="badge bg-blue-400 ml-2">{icon ? (<i className={icon}/>) : '#'}</span>
            )}

            <span className="ml-2">{title}</span>
            {rightTitle}
        </>
    )
}


function SidebarLayout({children, subTitle, content, ...props}) {
    const [Slot] = useSlot(children)

    return (
        <Space.Fill>
            <Space.Top size={45} order={1}>
                <Box display="flex" alignItems="center" height="100%" className="sb-title text-uppercase">
                    <Title {...props}/>
                </Box>
            </Space.Top>
            {subTitle && (
                <Space.Top size={36} order={2}>
                    <Box display="flex" height="100%" className="sb-sub-title">
                        {subTitle}
                        {/*<Slot name="subTitle"/>*/}
                    </Box>
                </Space.Top>
            )}
            <Space.Fill id="sb-body" scrollable={true}>
                {content}
                {/*<Slot name="content"/>*/}
            </Space.Fill>

        </Space.Fill>
    )
}

export default memo(SidebarLayout)