import React, { memo } from 'react'

function Pop(props = {}) {
    let link = '/admin/odich-sxh/update?id=786' + props.gid

    let items = [
        {title: 'Ngy mc bnh ca u tin', field: 'ngaymacbenh_1'},
        {title: 'Ngy xc nh', field: 'ngayxacdinh'},
        {title: 'Khu ph', field: 'khupho'},
        {title: 'PX', field: 'px'},
        {title: 'QH', field: 'qh'},
        {title: 'Ngy d kin kt thc', field: 'ngaydukienkt'},
        {title: 'Ngy ca gn nht', field: 'ngaymacbenh_last'},
        {title: 'S ca', field: 'soca'},
        {title: 'S ln x l', field: 'solan_xl'},
        {title: 'Tnh trng', field: 'tinhtrang'},
    ]

    return (
        <div>
            <div className="pop-wrapper">
                <div className="pop-title text-uppercase">
                    <a href={link} target="_blank"> dch #{props.id} ({props.soca})</a>
                </div>
                <div className="pop-body">
                    <ul className="list-data mb-10">
                        {items.map(({title, field}, k) => props[field] ? (<li key={k}><b>{title}:</b> <span>{props[field]}</span></li>) : '')}
                    </ul>
                    <ul className="list-actions list-inline mb-0 mt-1">
                        <li className="list-inline-item"><a href={link} target="_blank">Chi tit</a></li>
                    </ul>
                </div>
            </div>

        </div>
    )
}

export default memo(Pop)