import React, { memo } from 'react'
import { useInjectReducer, useInjectSaga } from '@core/utils/index'

import { LeafletContainer, sagas as mapSagas, reducer as mapReducer, PopupWMS, PopManager} from '@ttungbmt/redux-leaflet'

import '@ttungbmt/redux-leaflet/dist/redux-leaflet.cjs.css'

import OdichMap from './OdichMap'

import features from 'app/features'

function Leaflet() {
    useInjectReducer({key: 'map', reducer: mapReducer})
    useInjectSaga({key: 'map', saga: mapSagas.map})

    return (
        <LeafletContainer>
            <OdichMap/>

            <PopupWMS>
                <PopManager features={features}/>
            </PopupWMS>
        </LeafletContainer>
    )
}

export default memo(Leaflet)