import React, { memo } from 'react'
import { isEmpty, isString } from 'lodash-es'
import { useDispatch, useHistory, useSelector, emitter } from '@ttungbmt/redux-toolkit'
import Slot from '@core/components/Slot'
import { Tooltip } from '@core/components'
import { Nav, Dropdown } from 'react-bootstrap'
import { actions as layout_acts } from '@core/reducers/layout'
import { actions as mapActs } from '@ttungbmt/redux-leaflet'
import { selectors as pcdSels, actions as pcdActs } from 'app/reducers/pcd'

import Leaflet from './Leaflet'
import MapLayout from './Layout'

function ToolItem({icon, text}) {
    return <>{icon && <i className={icon}/>} <span className="md:hidden lg:inline">{text}</span></>
}

const NavItem = ({items, tooltip, onClick, ...props}) => {
    if (isEmpty(items)) return (
        <Tooltip title={tooltip}>
            <Nav.Link onClick={onClick}><ToolItem {...props}/></Nav.Link>
        </Tooltip>
    )

    return (
        <Tooltip title={props.tooltip}>
            <Nav.Item>
                <Dropdown as={Nav.Item}>
                    <Dropdown.Toggle as={Nav.Link}><ToolItem {...props}/></Dropdown.Toggle>
                    <Dropdown.Menu>
                        {items.map((v, k) => (isString(v) && v === '-') ? (
                                <div className="dropdown-divider" key={k}/>) :
                            <Dropdown.Item key={k} onClick={v.onClick}><ToolItem {...v}/></Dropdown.Item>)}
                    </Dropdown.Menu>
                </Dropdown>
            </Nav.Item>
        </Tooltip>
    )
}


function Map() {
    const dispatch = useDispatch()
    const history = useHistory()
    const {combineCir} = useSelector(pcdSels.getOdichConf)


    let lft_tool_items = [
            {icon: 'icon-feed', tooltip: 'Tm  dch t ng', onClick: () => history.push('/maps/odich/list')},
            {
                tooltip: combineCir ? 'Tch khoanh vng' : 'Gom khoanh vng',
                onClick: () => dispatch(pcdActs.setCombineCir(!combineCir)),
                icon: combineCir ? 'icon-ungroup' : 'icon-make-group'
            }
        ],
        rgt_tool_items = [
            {icon: 'icon-bin', tooltip: 'Xa im', onClick: () => {alert('Developing')}},
            {
                icon: 'icon-cube3',
                tooltip: 'Cng c',
                items: [
                    {
                        icon: 'icon-rulers',
                        text: 'o khong cch',
                        onClick: () => dispatch(mapActs.toggleControl('ruler'))
                    },
                    {icon: 'icon-pencil-ruler', text: 'o din tch', onClick: () => {alert('Developing')}},
                    {icon: 'icon-circles2', text: 'o bn knh', onClick: () => {alert('Developing')}},
                    '-',
                    {icon: 'icon-bookmark3', text: 'nh du v tr', onClick: () => {alert('Developing')}},
                    {icon: 'icon-share2', text: 'Chia s', onClick: () => {alert('Developing')}},
                    {icon: 'icon-printer', text: 'In bn ', onClick: () => {alert('Developing')}},
                    {icon: 'icon-image2', text: 'Xut file nh', onClick: () => emitter.emit('exportMap')},
                ]
            },
            {
                icon: 'icon-enlarge', tooltip: 'Ty chnh layout', items: [
                    {text: 'Bt/ tt header', icon: 'icon-sort', onClick: () => dispatch(layout_acts.toggleHeader())},
                    {text: 'Bt/ tt sidebar', icon: 'icon-tab', onClick: () => dispatch(layout_acts.toggleSidebar())},
                    {
                        text: 'M rng bn ',
                        icon: 'icon-width',
                        onClick: () => dispatch(layout_acts.toggleMapFullscreen())
                    },
                    {
                        text: 'Ton mn hnh',
                        icon: 'icon-screen-full',
                        onClick: () => dispatch(layout_acts.toggleAppFullscreen())
                    },
                ]
            },
        ]


    return (
        <MapLayout
            rightTool={<Nav>{rgt_tool_items.map((i, k) => <NavItem key={k} {...i}/>)}</Nav>}
            leftTool={<Nav>{lft_tool_items.map((i, k) => <NavItem key={k} {...i}/>)}</Nav>}
            leaflet={<Leaflet/>}
        />
    )
}

export default memo(Map)