import React, { memo } from 'react'
import { isEmpty } from "@ttungbmt/utils";

function Header({form}) {
    if (isEmpty(form.columns)) return <></>

    return (
        <thead>
        <tr>
            {form.columns.map(({label, width}, k) => <th key={k} style={{width}}>{label}</th>)}
        </tr>
        </thead>
    )
}


export default memo(Header)