import React, { memo, useContext } from 'react'
import { useDispatch, useSelector, useDidMount } from "@ttungbmt/react-hooks";
import { actions as odichActs, selectors as odichSels } from 'app/reducers/odich'
import { FormContext, useForm } from "react-hook-form"
import { get, head, last } from '@ttungbmt/utils'
import { Badge, ProgressBar } from 'react-bootstrap'
import Box from '@material-ui/core/Box'
import styles from 'styled-components'
import cx from 'classnames'
import { BtnTool } from "../../../components";
import FormSearch from "./FormSearch"
import { ContainerContext } from "../../../ContainerProvider";
import { setFormValues } from '@core/components/Form'

import moment from 'app/utils/moment'

const Wrapper = styles.div`
    .row-item {
        cursor: pointer;
        border-bottom: 1px solid #dfdfdf;
        &:hover {background: hsla(0, 0%, 92%, 0.8)};
        &.active {background: #ffeee8}
        > div {
            padding: 10px 7px;
        }
        .divider { padding: 10px 0; }
        .info-soca { width: 32px;};
    }
`

const Item = memo(({id, selected, cabenhs, ...props}) => {
    const dispatch = useDispatch()
    let firstCb = head(cabenhs) || {},
        lastCb = last(cabenhs) || {}

    const getData = (extra) => ({
        ...extra,
        id,
        props: {
            soca: cabenhs.length,
            ngaymacbenh_1: get(head(cabenhs), 'ngaymacbenh_nv'),
            ngaymacbenh_last: get(last(cabenhs), 'ngaymacbenh_nv')
        }
    })

    return (
        <Box display="flex" className={cx('row-item', {active: selected})} onClick={() => dispatch(odichActs.clickOdich(getData()))}>
            <Box><Badge variant="primary">{id}</Badge></Box>
            <Box><BtnTool title={'Xem chi tit'} icon="icon-eye8" onClick={() => dispatch(odichActs.clickOdich(getData({redirect: '/maps/odich/view/' + id})))}/></Box>
            <Box display="flex">
                <Box>
                    <div className="info-soca"><Badge pill variant="success">{cabenhs.length}</Badge></div>
                </Box>
                <Box>
                    <div className="font-weight-semibold">{firstCb.hoten}</div>
                    <div className="text-muted">{firstCb.ngaymacbenh_nv}</div>
                </Box>

            </Box>
            <Box className="divider">|</Box>
            <Box>
                <div>{lastCb.hoten}</div>
                <div className="text-muted">{lastCb.ngaymacbenh_nv}</div>
            </Box>
        </Box>
    )
})

const KEY = 'odich_auto'

function OdichListPage({SidebarLayout, defaultValues}) {
    const dispatch = useDispatch()
    const {getForm, actions: acts, loading} = useContext(ContainerContext)
    const form = getForm(KEY)

    const odichs = (useSelector(odichSels.getOdich) || []).filter(({id}) => id)

    const methods = useForm({
        defaultValues: {
            ...defaultValues,
            date_from: moment().subtract(3, 'months').format('L'),
            date_to: moment().format('L'),
        }
    })

    useDidMount(() => {
        if (form.values) setFormValues(form.values, methods)
    })

    const onSubmit = values => {
        acts.setFormValues({key: KEY, values})
        acts.fetchForm({
            form: {url: '/api/odich/tim-odich?table=cabenh_sxh'}, values: {
                ...values,
                ngmacbenh_from: values.date_from,
                ngmacbenh_to: values.date_to,
                distance: 200,
                day1: 14,
                day2: 30,
                table: 'cabenh_sxh',
            }
        }, {}, (data) => {
            dispatch(odichActs.createAutoOD(data))
        })
    }

    const onReset = () => {
        dispatch(odichActs.removeAll())
    }


    return (
        <FormContext {...methods} >
            <SidebarLayout
                title={`Danh sch  dch (${odichs.length})`}
                icon={`icon-list-numbered`}
                content={(
                    <>
                        <Wrapper>
                            <FormSearch form={form} {...methods} onSubmit={onSubmit} onReset={onReset} loading={loading}/>
                            {loading ? <ProgressBar className="m-2" animated now={100}/> : odichs.map((o, k) => <Item key={k} {...o}/>) }
                        </Wrapper>
                    </>
                )}
            >
            </SidebarLayout>
        </FormContext>

    )
}

export default memo(OdichListPage)