import React, { memo } from 'react'
import { InnerHTML } from '@core/components'
import { Tooltip } from '@core/components'
import cx from 'classnames'
import { isFunction, isUndefined, get, isString } from '@ttungbmt/utils'

const FirstColCell= memo(({items, model, html}) => {
    return (
        <div>
            <div className="font-weight-semibold"><InnerHTML html={html}/>
            </div>
            <ul className="list-inline mt-1">
                {items.map(({color, title, icon, href, visible, onClick}, k) => (
                    <li className={cx('list-inline-item', {color})} key={k}>
                        {(visible || isUndefined(visible)) ? (
                            <Tooltip title={title}>
                                {href ? <a href={isFunction(href) ? href(model) : href} target="_blank"><i className={icon}/></a> : <button className="btn-link" onClick={onClick}><i className={icon}/></button>}
                            </Tooltip>
                        ) : ''}
                    </li>
                ))}
            </ul>
        </div>
    )
})

function Cell({index, col, options, model}) {
    let field = col.attribute ? col.attribute : (isString(col.value) ? col.value : ''),
        text = get(model, field, '')

    return (
        <td>
            {index === 0 ? <FirstColCell items={options.items} html={text} model={model}/> : <InnerHTML html={text}/>}
        </td>
    )
}

export default memo(Cell)