import React, { memo, useEffect } from 'react'
import { renderRoutes } from 'react-router-config'
import SidebarLayout from 'app/layouts/map/partials/Main/Sidebar/Layout'
import { Template } from '@ttungbmt/redux-layout'
import { useSelector } from '@ttungbmt/redux-toolkit'
import ContainerProvider from './ContainerProvider'
import { selectors as authSel } from "@core/reducers/auth"

function MapsContainer({children, route, ...props}) {
    const auth = useSelector(authSel.getAuth)
    const defaultValues = {
        maquan: auth.user.maquan,
        maphuong: auth.user.maphuong,
    }

    return (
        <ContainerProvider>
            {renderRoutes(route.routes, {
                SidebarLayout, Template, defaultValues
            })}
        </ContainerProvider>
    )
}

export default memo(MapsContainer)

