import React, { memo } from 'react'
import { InnerHTML } from '@core/components'
import { useDispatch, useHistory } from '@ttungbmt/redux-toolkit'
import { get, map, isEmpty } from '@ttungbmt/utils'
import { actions as odichActs } from "app/reducers/odich"

import Cell from "../../../components/Cell"

function TableBody({form}) {
    const history = useHistory()
    const dispatch = useDispatch()
    const columns = get(form, 'columns')
    const data = get(form, 'data')
    if (!data && isEmpty(get(data, 'data'))) return <></>
    const id = 0

    return (
        <tbody>
        {map(data, ({cabenhs, ...props}, k1) => {
            let onRowClick = (e) => dispatch(odichActs.clickOdich({cabenhs, props, id})),
                cellOptions = {
                    items: [
                        {
                            title: 'Xem chi tit', color: 'text-success', icon: 'icon-location4', onClick: (e) => {
                                e.stopPropagation()
                                history.push('/maps/odich/view/'+id)
                                onRowClick(e)
                            }
                        },
                        {
                            title: 'Chnh sa',
                            color: 'text-primary',
                            icon: 'icon-pencil7',
                            href: ({gid}) => `/admin/odich-sxh/update?id=${gid}`
                        },
                    ]
                }


            return (
                <tr key={k1} onClick={onRowClick}>
                    {columns.map((col, k2) => <Cell key={k2} index={k2} col={col} options={cellOptions}
                                                    model={props}/>)}
                </tr>
            )
        })}
        </tbody>
    )
}

export default memo(TableBody)