import React, { memo, useContext, cloneElement } from 'react'
import { useDidUpdate, useDidMount } from '@ttungbmt/react-hooks'
import { ProgressBar } from 'react-bootstrap'

import { ContainerContext } from "../../ContainerProvider";
import { Header, Wrapper } from "./index";
import $ from 'jquery'

function TableContent({children, form, style}) {

    useDidMount(() => {
        setTimeout(() => {
            let $sbBody = $('#sb-body'),
                height = $sbBody.height() - $sbBody.find('form').height()

            $sbBody.find('.tb-scrollable')
                .css('max-height', height)
                .freezeTable({
                    scrollable: true,
                    shadow: true,
                })
        }, 100)
    })

    return (
        <table className="table table-sm table-bordered" style={{...style}}>
            <Header form={form}/>
            {cloneElement(children, {form})}
        </table>
    )
}

function Table({style, children, form, name: key, url}) {
    const {loading, actions: acts} = useContext(ContainerContext)

    useDidMount(() => {
        if (!form.columns) acts.fetchColumns({key, url})
    })

    useDidUpdate(() => {
        if (form.columns) acts.fetchData({form})
    }, [form.page, JSON.stringify(form.values)])


    return (
        <Wrapper>
            <div className="table-responsive tb-scrollable">
                {loading ? <ProgressBar animated now={100} className="m-2"/> : form.columns &&
                    <TableContent form={form} style={style}>{children}</TableContent>}
            </div>
        </Wrapper>

    )
}

export default memo(Table)